* {
  margin: 0;
  padding: 0; }

body {
  font-family: sans-serif;
  background-color: #fff; }

.headerBG {
  text-align: center;
  color: #fff;
  height: 237px;
  background: url("./images/moneyBG.jpeg") no-repeat;
  background-size: 100%;
  text-outline: 2px 2px black; }
  .headerBG span {
    color: #0962dd;
    font-size: 7rem;
    padding: 0; }
  .headerBG h1 {
    padding-bottom: 16px; }

.navBar {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  line-height: 3.4rem;
  bottom: 390px; }
  .navBar ul {
    list-style: none;
    display: -ms-inline-flexbox;
    display: inline-flex; }
    .navBar ul a {
      text-decoration: none;
      color: #000; }
    .navBar ul li {
      padding: 0 15px;
      background-color: #0962dd;
      margin: 0 10px 0 0; }
    .navBar ul li:hover {
      background-color: #222;
      color: white; }

.content {
  background-color: #9bcbe7;
  min-height: 520px; }
  .content h2 {
    text-align: center;
    padding: 20px 0;
    font-size: 30px; }
  .content .desc {
    font-size: 25px;
    text-align: center; }
  .content .addBtn {
    max-width: 1200px;
    margin-right: auto;
    margin-left: auto; }
    .content .addBtn .submit {
      font-size: 25px;
      vertical-align: middle;
      background-color: #0962dd;
      border: none;
      padding: 10px;
      float: right; }
    .content .addBtn .submit:hover {
      background-color: #000;
      color: #fff; }
  .content article {
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px; }
    .content article p {
      font-size: 25px;
      padding-bottom: 20px; }

footer {
  background-color: #000;
  color: #fff;
  text-align: center;
  height: 4rem;
  line-height: 4rem;
  width: 100%; }
  footer p {
    display: inline-block;
    vertical-align: middle; }



